<template>
  <div class="feedback">
    <div class="topnav">
      <van-nav-bar
        title="帮助反馈"
        left-text="三( ᐛ )"
        @click-left="onClickLeft"
      />
    </div>
    <div class="one">
      <div class="top">问题类型</div>
      <div class="boxes">
        <div
          class="box"
          :class="{ boxcolor: boxcolora }"
          @click="(boxcolora = !boxcolora), (boxFlag = false)"
        >
          漫画缓存
        </div>
        <div
          class="box"
          :class="{ boxcolor: boxcolorb }"
          @click="(boxcolorb = !boxcolorb), (boxFlag = false)"
        >
          图片章节
        </div>
        <div
          class="box"
          :class="{ boxcolor: boxcolorc }"
          @click="(boxcolorc = !boxcolorc), (boxFlag = false)"
        >
          支付问题
        </div>
        <div
          class="box"
          :class="{ boxcolor: boxcolord }"
          @click="(boxcolord = !boxcolord), (boxFlag = false)"
        >
          网络
        </div>
        <div
          class="box"
          :class="{ boxcolor: boxcolore }"
          @click="(boxcolore = !boxcolore), (boxFlag = false)"
        >
          优化建议
        </div>
        <div
          class="box"
          :class="{ boxcolor: boxcolorf }"
          @click="(boxcolorf = !boxcolorf), (boxFlag = false)"
        >
          其他
        </div>
      </div>
    </div>
    <div class="two">
      <div class="top">反馈内容</div>
      <textarea
        name="email"
        placeholder="在此输入反馈意见"
        v-model="textareaLength"
      ></textarea>
      <div class="fonts">
        <van-icon name="photo-o" />
        <span>{{ textareaLength.length }}/1000</span>
      </div>
    </div>
    <div class="three">
      <div class="top">联系邮箱</div>
      <van-icon name="envelop-o" />
      <input
        type="text"
        placeholder="输入邮箱地址可获得反馈"
        v-model="inputLength"
      />
      <button @click="vantToast">提交</button>
    </div>
    <div class="four">
      <div class="top">常见问题回答</div>
      <van-collapse v-model="activeName" accordion>
        <van-collapse-item title="如何取消连续包月/自动续费？" name="1">
          <p>你可通过下述方式取消已开通的连续包月服务:</p>
          <p>
            1.若是安卓设备,在App内点击【我的--VIP会员】--点击【管理自动续费】--点击【取消自动续费】即可。
          </p>
          <p>
            ⒉.若是IOS设备,打开系统【设置】--进入_【iTunes
            Store与AppStore】--点击查看【Apple
            ID】,在【账户设置】页面点击【订阅】，找到相关的订阅记录，点击【取消订阅】即可
          </p>
          <p>
            注:您可以随时取消或开通自动续费服务。取消自动续费后，不影响您权益的有效时长,到期后将不再自动扣费。
          </p>
        </van-collapse-item>
        <van-collapse-item title="什么是金币？" name="2">
          <p>
            金币是指用户通过知音漫客客户端任务、活动等获取的限期免费币种-元宝和金币的汇率为1:10
          </p>
          <p>获得规则:免费获取</p>
          <p>
            使用规则:普通任务获得金币获取后有效期为30天。活动获得金币根据具体活动设定有效期执行。购买漫画付费章节，当用户同时有元宝和金币时,优先消耗金币。购买行为完成后，金币将自动扣除
          </p>
        </van-collapse-item>

        <van-collapse-item title="什么是推荐票？" name="3">
          <p>推荐票是指可以用以支持自己喜爱的作品的道具的一种，可免费获得。</p>
          <p>
            获得规则:用户每次首次保持登录免费赠送10张推荐票,用户等级越高，赠送推荐票越多，无论使用不使用,当日清零
          </p>
          <p>
            使用规则:在作品支持页，点击推荐图标投出推荐票,每个作品每用户永久只能投一推荐票，每天可给多个作品技狂存票。
          </p>
        </van-collapse-item>

        <van-collapse-item title="什么是月票？" name="4">
          <p>月票是知音漫客针对漫画读者推出的专属道具,可免费获得。</p>
          <p>
            获得规则:每个月用户首次保持登录时系统会自动赠送—张月票,用户等级越高,赠送月票越多，无论使用不使用,月底不再保留。
          </p>
          <p>
            使用规则:在作品支持页,点击月票图标投出月票支持作品,投出后不可撤销。
          </p>
        </van-collapse-item>

        <van-collapse-item title="什么是元宝？" name="5">
          <p>
            元宝是在知音漫客客户端使用的虚拟货币,可以用于阅读付费章节、打赏礼物等。
          </p>
          <p>获得规则:通过充值获得元宝。</p>
          <p>
            使用规则:购买漫画付费章节，所消耗元宝,不同章节,定价有所不同。购买行为完成后，元宝将自动扣除。购买的付费章节，可直接浏览,不用再次付费。
          </p>
        </van-collapse-item>

        <van-collapse-item title="自动续费已扣款，但VIP会员没有续期？" name="6">
          <p>
            1.若您已开启自动续费服务,在发生自动扣费后仍未成功续期会员，可重启App或重新登录账号，并查看是否有续费记录
          </p>
          <p>2.若仍未解决，请联系客服解决</p>
        </van-collapse-item>

        <van-collapse-item title="自动续费失败后，会取消自动续费吗？" name="7">
          <p>
            1.若您已经开通自动续费服务,下一续费周期余额不足时会导致自动扣款失败,但此时并没有取消自动续费，系统会在余额充足时再次尝试自动扣款。
          </p>
          <p>2.若需取消自动续费,可查看教程:“如何取消连续包月/自动续费?”</p>
          <p>
            3.若成功取消自动续费,则在“我的”--“消息"里会收到取消自动续费成功通知。
          </p>
        </van-collapse-item>

        <van-collapse-item
          title="为什么开通了连续包月，却没有按时自动续费呢？"
          name="8"
        >
          <p>
            可能是因为您签约的自动扣款账号余额不足，或者是您开通自动续费后，关闭了签约账号的自动扣款支付。
          </p>
          <p>
            例如签约账号是支付宝,可在支付宝App-我的-设置-支付设置-免密支付/自动扣款中，查看是否有我方App的签约代扣服务。
          </p>
          <p>若以上扣款情况均不符合,请联系客服解决</p>
        </van-collapse-item>

        <van-collapse-item title="如果重复购买了VIP会员怎么办？" name="9">
          <p>
            同一账号开通两次相同的会员，会员有效期将会根据总购买时长顺延,不会重叠时间。请您核实账号的会员到期时间。
          </p>
          <p>
            已经开通会员(连续包月)的账号仍可购买非连续包月套餐，会员有效期将会根据总购买时长进行顺延。
          </p>
        </van-collapse-item>
      </van-collapse>
    </div>
  </div>
</template>

<script>
// import { Toast } from "vant";

export default {
  data() {
    return {
      textareaLength: "",
      activeName: "",
      inputLength: "",
      boxcolora: false,
      boxcolorb: false,
      boxcolorc: false,
      boxcolord: false,
      boxcolore: false,
      boxcolorf: false,
      boxFlag: true,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    vantToast() {
      if (
        this.boxFlag &&
        this.textareaLength == 0 &&
        this.inputLength.length == 0
      ) {
        this.$toast({message:"亲，你什么都没写喔",position: 'top'});
      } else if (this.boxFlag && this.textareaLength == 0) {
        this.$toast({message:"请选择问题类型和反馈意见",position: 'top'});
      } else if (this.inputLength.length == 0 && this.textareaLength == 0) {
        this.$toast({message:"请选择反馈意见和邮箱地址",position: 'top'});
      } else if (this.inputLength.length == 0 && this.boxFlag) {
        this.$toast({message:"请选择问题类型和邮箱地址",position: 'top'});
      } else if (this.inputLength.length == 0) {
        this.$toast({message:"请输入邮箱地址",position: 'top'});
      } else if (this.textareaLength == 0) {
        this.$toast({message:"请输入反馈意见",position: 'top'});
      } else if (this.boxFlag) {
        this.$toast({message:"请选择问题类型",position: 'top'});
      } else {
        this.$toast.success("反馈成功");
      }
    },
  },
  created() {
    this.$store.commit("falseFooterFlag");
  },
};
</script>

<style lang='less'>
.van-toast--text{
    background: #fc6454;
}
.feedback {
  .topnav {
    width: 100%;
    font-size: 18px;
    .van-nav-bar__content {
      border-bottom: 1px solid #f1f1f1;
      .van-nav-bar__left {
        span {
          color: #fc6454;
        }
      }
      .van-ellipsis {
        color: #fc6454;
      }
    }
  }
  .one {
    padding: 10px 20px;
    .top {
      padding-left: 15px;
      border-left: 3px solid #fc6454;
    }
    .boxes {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      margin-top: 20px;
      .box {
        width: 30%;
        height: 50px;
        border: 1px solid #eaeaea;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        margin: 10px 0;
        color: #999;
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6) {
          margin-top: 0;
        }
      }
      .boxcolor {
        background: #fc6454;
        color: white;
      }
    }
  }
  .two {
    padding: 10px 20px;
    position: relative;
    .top {
      padding-left: 15px;
      border-left: 3px solid #fc6454;
    }
    textarea {
      resize: none;
      width: 100%;
      height: 200px;
      padding: 20px 20px 30px 20px;
      margin: 20px 0 0;
      border-radius: 5px;
      border-color: #eaeaea;
      &::-webkit-input-placeholder,
      textarea::-webkit-input-placeholder {
        color: #999999;
      }
    }
    .fonts {
      position: absolute;
      bottom: 20px;
      left: 40px;
      width: calc(100% - 80px);
      display: flex;
      align-items: center;
      justify-content: space-between;
      i {
        font-size: 25px;
        color: #fc6454;
      }
    }
  }
  .three {
    padding: 10px 20px;
    position: relative;
    i {
      position: absolute;
      top: 60px;
      font-size: 25px;
      color: #fc6454;
    }
    .top {
      padding-left: 15px;
      border-left: 3px solid #fc6454;
    }
    input {
      width: 100%;
      height: 50px;
      margin-top: 20px;
      padding: 0 0 5px 30px;
      font-size: 18px;
      border: none;
      border-bottom: 1px solid #d5d5d5;
      &::-webkit-input-placeholder,
      textarea::-webkit-input-placeholder {
        color: #999999;
      }
    }
    button {
      width: 100%;
      height: 50px;
      background: #fc6454;
      border: none;
      outline: none;
      margin-top: 20px;
      color: white;
    }
  }
  .four {
    padding: 10px 20px;
    position: relative;
    .top {
      padding-left: 15px;
      border-left: 3px solid #fc6454;
    }
    p {
      padding: 5px 0;
    }
  }
}
</style>